export default ({ IDL }) => {
  const ICPunk = IDL.Service({
    'claim' : IDL.Func([IDL.Nat], [IDL.Bool], []),
    'countClaimedTokens' : IDL.Func([], [IDL.Nat], ['query']),
    'listTokens' : IDL.Func([], [IDL.Vec(IDL.Opt(IDL.Principal))], ['query']),
    'name' : IDL.Func([], [IDL.Text], ['query']),
    'symbol' : IDL.Func([], [IDL.Text], ['query']),
    'totalSupply' : IDL.Func([], [IDL.Nat], ['query']),
    'transferFrom' : IDL.Func([IDL.Principal, IDL.Nat], [IDL.Bool], []),
    'userTokens' : IDL.Func([IDL.Principal], [IDL.Vec(IDL.Nat)], ['query']),
  });
  return ICPunk;
};
export const init = ({ IDL }) => { return []; };